.spinner {
    border: 1 solid hsl(0, 25%, 98%); 
    border-top: 16px solid #0b2c42; 
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }